import html2pdf from 'html2pdf.js'
export const htmlTopdf = (section,option={}) => {
    //  DOM 
    const element = document.querySelector(section)
    if (!element) {
        throw ('DOM')
    }
    //  html2pdf 
    const options = {
        margin: 0,
        filename: 'file.pdf',
        image: {
            type: 'jpeg',
            quality: 0.98
        },
        html2canvas: {
            scale: 3, // 
            useCORS: true // 
        },
        jsPDF: {
            unit: 'in',
            format: 'a4',
            orientation: 'portrait'
        },
        ...option
    }
    //  DOM  PDF 
    html2pdf().set(options).from(element).save()
}